.intro-dialogbox--bg {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #0000008f;
  z-index: 4;
}

.intro-dialogbox--bg.hide {
  display: none;
}

.intro-dialogbox--close-button {
  margin-top: 1em;
  border: 1px solid #444;
  padding: 0.5em 2em;
  background-color: #fff;
  font-family: Sohne, sans-serif;
  display: inline-block;
  font-size: 1rem;
}

.intro-dialogbox--frame {
  min-width: 500px;
  max-width: 750px;
  background-color: #fffc;
  border: 1px solid #000;
  padding: 2rem 4rem;
  line-height: 1.5;
  box-sizing: border-box;
}

.intro-dialogbox--options {
  text-align: center;
}

.intro-dialogbox--title {
  text-align: center;
}
